body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  letter-spacing: 1px; }

h1 {
  font-size: 48px; }

.summoner-watcher-header {
  width: calc(100vw - 100px);
  background-color: #2E314D;
  height: 30vh;
  margin: 0;
  max-height: 200px;
  min-height: 100px;
  padding: 50px;
  display: flex; }
  .summoner-watcher-header .header-content {
    width: 1300px;
    margin: auto; }
  .summoner-watcher-header h1 {
    color: #fff;
    margin-bottom: 25px; }
  .summoner-watcher-header nav {
    padding: 10px 0px;
    font-size: 20px;
    border-top: 1px solid #43497C;
    color: #fff;
    width: max-content; }
    .summoner-watcher-header nav a {
      padding: 10px 20px 10px 20px;
      user-select: none;
      transition: .1s; }
    .summoner-watcher-header nav a:hover {
      border-top: 1px solid #D88232; }
    .summoner-watcher-header nav a:active {
      border-top: 1px solid #45C642; }
    .summoner-watcher-header nav a:first-child {
      padding: 10px 20px 10px 0px; }
